import React ,{useState} from 'react'
import './UnEvento.css'


const UnEvento = () => {
    
    const[banca,setBanca]=useState(0) 
   
    const[evento1,setEvento1]=useState(0)
    const[cuotaEvento1,setCuotaEvento1]=useState(0)
    
    
    let[estado,setEstado]=useState('')
    let[ev,setEv]=useState(0)
    let[acierto,setAcierto]=useState(0) 
    let[descripcion,setDescripcion]=useState('')
    
    const cajas10=banca/20*0.1 
    const cajas20=banca/20*0.2
    const cajas30=banca/20*0.3
    const cajas40=banca/20*0.4
    const cajas50=banca/20*0.5
    const cajas60=banca/20*0.6
    const cajas70=banca/20*0.7
    const cajas80=banca/20*0.8
    const cajas90=banca/20*0.9
    const cajas100=banca/20*1
    
    

     
   const contenidoBanca=(e)=>{
      e.preventDefault()
         
   
      banca/40 ? console.log('bien'):console.log('mal')
         
      console.log(cajas10)
   }
   

   const UnEvento=(e)=>{
    e.preventDefault()
    
        
        evento1 && cuotaEvento1   ?  

        setEstado(estado="Datos introducidos correctamente")  :  setEstado(estado="Rellene todos los campos")
       
        if((((evento1/100)*cuotaEvento1)/1)===1){
           
                    setEv(ev+=(((evento1/100)*cuotaEvento1))/1)
                    setAcierto(acierto=100-((100-evento1)))
                    setDescripcion(descripcion='Probabilidad neutra  ¡No apostar! , moneda al aire ni ganas ni pierdes a largo plazo')
             
        }else if((((evento1/100)*cuotaEvento1))/1<1){
           
           setEv(ev+=(((evento1/100)*cuotaEvento1))/1)
           setAcierto(acierto=100-((100-evento1)))
           setDescripcion(descripcion='Probabilidad negativa EV- ¡No apostar! en ningún caso, perdidas a largo plazo')
                    
            
                    }else{

           setEv(ev+=(((evento1/100)*cuotaEvento1))/1)
           setAcierto(acierto=100-((100-evento1)))
           setDescripcion(descripcion='Esperanza por EV+ ,sí el % de acierto esta bién puedes apostar')
           
                
        
                } 
        
    
    
    
    
    
    
    
    }     
    
    
    
    
    
    
    
    
    return (
        <>
            
      <div className="container d-block">

        <h1>1 EVENTO</h1>
       <h5>BANKROLL/BANCA</h5>
           <form  onSubmit={contenidoBanca}>
           <input type="text" onChange={(e)=>setBanca(e.target.value)} className="col col1" placeholder=""/>
         
     
      
           <button id="caja" onClick={contenidoBanca} type="onsubmit">ENVIAR</button>
           </form>
   
           <h5>RESULTADOS</h5>
           <h3>Nula Confianza(stake1-10%):<br></br>{cajas10}<br></br></h3> 
           <h3>Nula Confianza(stake2-20%):<br></br>{cajas20}<br></br></h3> 
           <h3>Poca Confianza(stake3-30%):<br></br>{cajas30}<br></br></h3> 
           <h3>Poca Confianza(stake4-40%):<br></br>{cajas40}<br></br></h3> 
           <h3>Media Confianza(stake5-50%):<br></br>{cajas50}<br></br></h3> 
           <h3>Media Confianza(stake6-60%):<br></br>{cajas60}<br></br></h3> 
           <h3>Alta Confianza(stake7-70%):<br></br>{cajas70}<br></br></h3> 
           <h3>Alta Confianza(stake8-80%):<br></br>{cajas80}<br></br></h3> 
           <h3>Total Confianza(stake9-90%):<br></br>{cajas90}<br></br></h3> 
           <h3>Total Confianza(stake10-100%):<br></br>{cajas100}<br></br></h3> 
         
       
       
       
       
       <hr></hr>
     
           
       
        <div id="formulario">
        <form onSubmit={UnEvento}>
         <h1>A UN EVENTO</h1>
         <h2>COMBINADAS</h2>
         <label htmlFor="evento"><h3>Evento 1</h3></label>
        <input className="evento" onChange={(e)=>setEvento1(e.target.value)} type="text"/>
         <label htmlFor=""><h3>Cuota Evento 1</h3></label>
        <input className="evento" onChange={(e)=>setCuotaEvento1(e.target.value)} type="text"/>
         
        
        
        <hr></hr>
        <h3>RESULTADOS</h3>
       
       <h3><span>Estado:</span>{estado}</h3>   
       <h3><span>EV:</span>{ev}</h3>     
       <h3><span>Acierto:</span>{acierto}%</h3>      
       <h3><span>Descripcion:</span>{descripcion}</h3>  
      
       
        <button id="calc" type="onsubmit" onClick={(e)=>setDescripcion(descripcion)} onClick={(e)=>setAcierto(acierto)} onClick={(e)=>setEv(ev)} onClick={(e)=>setEstado(estado)}>ENVIAR</button>
        </form>
      
       
       
        </div>

       
       
       
       
       
       
      
      
      
      
      
      
      
      
      
      
      
      
       </div>


  







     
       
       
       
       
       
       
       
       
       
        </>
    )
}

export default UnEvento
